<template>
  <div>
	<div class="main">
        <div class="titBkL">
            登陆
        </div>
        <el-form :model="numberValidateForm" :rules="rules" ref="numberValidateForm" label-width="100px" class="demo-ruleForm">
            <el-form-item
            label="用户名"
            prop="ph">
            <el-input v-model.number="numberValidateForm.ph" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
            label="密码"
            prop="password">
            <el-input type="password" v-model.number="numberValidateForm.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
            <el-button type="primary" @click="submitForm('numberValidateForm')">登陆</el-button>
            </el-form-item>
        </el-form>
	</div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapActions } from 'vuex'
  export default {
    data() {
      return {
        numberValidateForm: {
          ph:'',
          password:''
        },
        rules:{
          ph:[
            { required: true, message: '请输入用户名', trigger: 'blur' },
          ],
          password:[
            { required: true,pattern:/^(\w){6,20}$/, message: '请输入6-20个字母、数字、下划线' ,trigger: 'blur' },
          ]

        }
      };
    },
    methods: {
      ...mapActions(['getBuserInfo']),
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            url.blogin(this.numberValidateForm)
            .then((res)=>{
                console.log(res,'--------------后台登陆');
              if(res.data.code[0]=="200"){
                let xx=res.data.data[0].fields
                xx.user_id=res.data.data[0].pk
                this.getBuserInfo(xx)
                sessionStorage.setItem('bkL', 'L成功');
                // 跳转到后台---员工管理
                this.$router.push('/bkIndex/bkkeHu')
              }else{
                this.$message.error(res.data.message[0]);
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>


<style scoped>
.titBkL{
    font-size:34px;
    text-align:center;
    line-height:80px;  
}
.demo-ruleForm{
    margin-right: 60px
}
.main{
    text-align: center; /*让div内部文字居中*/
    background-color: #f1f1f1;
    border-radius: 20px;
    border: 1px solid #cccccc;
    width: 350px;
    height: 290px;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
</style>


